import {post, get} from '@/axiosConfig/axiosConfig.js'

const login = (name, password) => {
  post({
    url: '/user/login',
    params: {
      name: name,
      password: password
    }
  })
}

const register = (name, password, email) => {
  get({
    url: '/user/register',
    params: {
      name: name,
      password: password,
      email: email
    }
  })
}

const getUserInfo = (userId) => {
  get({
    url: '/user/getUserInfo',
    params: {
      _id: userId
    }
  })
}

export default {
  login,
  register,
  getUserInfo
}



