import Vue from 'vue'
import Vuex from 'vuex'
import indexPage from './modules/indexPage'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  modules: {
    indexPage
  },
  strict: debug
})



